
/* eslint-disable @typescript-eslint/no-explicit-any */
import { defineComponent } from "vue";
import { NCard, NSpace, NInput, NSelect, NButton } from "naive-ui";
import axios from "axios";
import { MsgErrorHandler } from "@/components/Common/Msg/MessageApi/Message";
export default defineComponent({
  name: "VideoParsePlus",
  data() {
    return {
      videoSrc: "",
      videoUrl: "",
      jks: [],
      canUseJks: [], // jks
      checkCount: 1,
      currentUrl: "", // 
      selectInterfaces: [], // 
    };
  },
  components: {
    NCard,
    NSpace,
    NInput,
    NSelect,
    NButton,
  },
  mounted() {
    let jks: Array<any> = [];
    // 
    // let url = "http://tv.wandhi.com/go.html";
    let parseUrl = "http://cdn.wandhi.com/js/tv/player.js";
    axios
      .get(parseUrl)
      .then((res) => {
        eval(res.data);
        console.log(jks);
        let obj: any = jks;
        this.jks = obj;
      })
      .catch((err) => {
        MsgErrorHandler(":" + err);
      });
    setTimeout(() => {
      console.log(", :", this.checkCount);
      this.checkJks();
      this.checkCount += 60 * 2;
    }, this.checkCount * 1000);
  },
  methods: {
    checkUrl(url: string) {
      return axios.get(url);
    },
    checkJks() {
      if (this.jks.length > 0) {
        // 
        let urls: Array<any> = this.jks;
        urls.forEach((item) => {
          this.checkUrl(item.url)
            .then((res) => {
              item.valid = true;
              let jkObj: Array<any> = [];
              let ins: Array<any> = [];
              this.jks.forEach((u: any) => {
                if (u.valid === true) {
                  jkObj.push(u);
                  ins.push({ label: u.title, value: u.url });
                }
              });
              let obj: any = jkObj;
              let inss: any = ins;
              this.canUseJks = obj;
              this.selectInterfaces = inss;
            })
            .catch((err) => {
              item.valid = false;
            });
        });
        console.log(this.canUseJks); // 
      }
    },
    changeInterface() {
      console.log(":", this.currentUrl);
    },
    toVipVideo() {
      if (!this.currentUrl || this.currentUrl === "") {
        MsgErrorHandler("");
      }
      if (!this.videoUrl || this.videoUrl === "") {
        MsgErrorHandler("");
      }
      this.videoSrc = this.currentUrl + this.videoUrl;
    },
  },
});
